@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,600;0,700;1,300;1,400;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10vh;
    background: #4285F4;
    color: white;
}

nav h1 {
    color: white;
    font-weight: bold;
    font-size: 1.2em;
}

.nav-links {
    width: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    list-style-type: none;
}

.nav-links .link {
    text-decoration: none;
}

.nav-links .link li {
    color: white;
    font-weight: bolder;
    font-size: 0.7em;
}



.login-page-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 65vh;
}

.login-card-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 45vw;

}

.login-title {
    text-transform: uppercase;
    font-size: 1.2em;
    font-weight: bold;
}

.login-form {
    display: flex;
    flex-direction: column;
}

.login-input {
    margin-top: 0.5em;
    padding: 0.2em 0.5em;
    border-radius: 0.5em;
    border: none;
    background-color: #E0E0E0;
}

.submit-button {
    margin-top: 1em;
    padding: 0.3em;
    border-radius: 0.5em;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 0.8em;
    font-weight: bold;
    background-color: #4285F4;
    color: white;
}

.submit-button:hover {
    transform: scale(1.03);
}

* {
	margin: 0px;
	padding: 0px;
	box-sizing: border-box;
	font-family: 'Source Sans Pro';
	font-weight: 400;
	font-size: 20px;
	line-height: 150%;
	background-color: rbga(255,255,255,0);
	color: #454545;
}

.page-container {
	margin-left: 15%;
	margin-right: 15%;
	/*padding-bottom: 48px;*/
} 
#home-page {
    display: flex;
    justify-content: center;
    align-items: flex-end;
}
.home-page-container {
    display: flex;
    justify-content: space-between;
    width: 80vw;
    height: 80vh;
}

.home-page-container > .welcome {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 50%;
}

.home-page-container > .welcome > div > p {
    font-size: 2.3em;
    font-weight: bold;
}


.home-page-container > .login {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
}
.participant-view {
  margin: 0 auto;
  height: 60vh;
  width: 100vh;
  border: 1px solid green;
  margin-top: 5vh;
}

.self-view {
  margin: 0 auto;
  height: 18vh;
  width: 30vh;
  border: 1px solid blue;
  font-size: 0.5em;
  margin-top: 5vh;
}


